export default {
	data() {
		return {
			height: '',
			items: ["今日订单", "历史订单"],
			current: 0,
			merchantId: '',
			orders: [],
			opens: -1,
			tg: true,
			height_a: "1px",
			orderDays: [],
			index: 0,
			page: 1,
			histor_page:1,
		}
	},
	onLoad() {
		let that = this;
		uni.getStorage({
			key: 'userinfo',
			success(e) {
				that.merchantId = e.data.id;
				that.historical_order();
				that.orderDay();
			}
		});
	},
	// 上拉加载
	onReachBottom() {
		if (this.index == 0) {
			this.page++;
			this.orderDay();
		} else if (this.index == 1) {
			this.histor_page++;
			this.historical_order();
		}
	},
	onReady() {
		this.getTopheight();
	},
	methods: {
		getTopheight: function() {
			let that = this;
			uni.getSystemInfo({
				success(res) {
					that.height = res.statusBarHeight + 44 + "px";
					that.height_a = res.statusBarHeight + 44 + 50 + "px";
				}
			})
		},
		onTap: function(index) {
			this.index = index;
			if (this.current != index) {
				this.current = index;
			};
		},
		historical_order: function() {
			let that = this;
			let neworder = [];
			uni.showLoading({
				
			});
			uni.request({
				url: this.serveipd + '/api/merchant/order/orderStatistics',
				method: 'GET',
				data: {
					merchantId: that.merchantId,
					page: that.histor_page
				},
				success: res => {
					uni.hideLoading();
					neworder = res.data.data;
					console.log(neworder)
					if (neworder.length == 0) {
						uni.showToast({
							title: '没有更多数据',
							icon: 'none'
						});
						return
					}
					that.orders = that.orders.concat(neworder);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		orderDay: function() {
			let that = this;
			let neworder = [];
			uni.showLoading({
				
			});
			uni.request({
				url: this.serveipd + '/api/merchant/order/orderDay',
				method: 'GET',
				data: {
					merchantId: that.merchantId,
					page: that.page
				},
				success: res => {
					uni.hideLoading();
					neworder = res.data.data;
					if (neworder.length == 0) {
						uni.showToast({
							title: '没有更多数据',
							icon: 'none'
						});
						return
					}
					that.orderDays = that.orderDays.concat(neworder);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		open: function(index) {
			this.tg = !this.tg;
			if (this.opens != index) {
				this.opens = index;
			} else {
				this.opens = -1;
			}
		},
		wz: function(latitude, longitude) {
			uni.openLocation({
				latitude: latitude,
				longitude: longitude,
				success: function() {
					console.log('success');
				}
			});
		},
		dh: function(phone) {
			uni.makePhoneCall({
				phoneNumber: phone,
				success: function(res) {
					console.log(res)
				}
			});
		},
	}
}
